  new SplitText(".section1 h1", {type: "words,chars"});


//Magnetic btn

var magnets = document.querySelectorAll('.magnetic')
  var strength = 50

  magnets.forEach( (magnet) => {
    magnet.addEventListener('mousemove', moveMagnet );
    magnet.addEventListener('mouseout', function(event) {
      TweenMax.to( event.currentTarget, 1, {x: 0, y: 0, ease: Power4.easeOut})
    } );
  });

  function moveMagnet(event) {
    var magnetButton = event.currentTarget
    var bounding = magnetButton.getBoundingClientRect()

    //console.log(magnetButton, bounding)

    TweenMax.to( magnetButton, 1, {
      x: ((( event.clientX - bounding.left)/magnetButton.offsetWidth) - 0.5) * strength,
      y: ((( event.clientY - bounding.top)/magnetButton.offsetHeight) - 0.5) * strength,
      ease: Power4.easeOut
    })

    //magnetButton.style.transform = 'translate(' + (((( event.clientX - bounding.left)/(magnetButton.offsetWidth))) - 0.5) * strength + 'px,'+ (((( event.clientY - bounding.top)/(magnetButton.offsetHeight))) - 0.5) * strength + 'px)';
  }



 


  //Since
  var date = new Date().getFullYear();
  $('.section2 .since-col span').html(date);
  var count = new Date().getFullYear();
  var i = setInterval(function(){
    $(".section2 .since-col span").html(count);
    
    counter--;
    count--;
    if(count == 2017){
      clearInterval(i);
    }
  },530);

  //Timezone ATH
  fetch('http://worldtimeapi.org/api/timezone/Europe/Athens')
  .then(function (response) {
      return response.json();
  })
  .then(function (data) {
      $('span.time').html(data.datetime);

      var input = $('span.time').html();

      var fields = input.split('T');

      var date = fields[0];
      var fullTime = fields[1];

      var finalTime = fullTime.split('.');

      var time = finalTime[0];

      var finalTimeSplitted = time.split(':');

      var timeFinal = finalTimeSplitted[0];
      var timeFinal2 = finalTimeSplitted[1];

      $('span.time').html(timeFinal + '<span class="fadeInOut">:</span>' + timeFinal2);
      console.log(timeFinal)
      console.log(timeFinal2)
    })
  .catch(function (err) {
      console.log('error: ' + err);
  });

